        <!-- 建议卡片 -->
        <div class="suggestion-card">
          <div class="suggestion-title">改进建议</div>
          <div class="suggestion-content">
            <div v-if="isLoading" class="loading-suggestion">
              <div class="loading-spinner-small"></div>
