              <div class="loading-text-small">评估中...</div>
            </div>
            <div v-else>
              <div class="card-title">{{ radarIndicator[index] ? radarIndicator[index].name : `维度${index + 1}` }}</div>
              <div class="card-score">评分: {{ radarData[index] || 0 }}/5</div>
              <div class="card-reason">{{ reason || '暂无评分理由' }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <style scoped>
