<script>
import * as echarts from 'echarts';
import axios from 'axios';

export default {
  name: 'Evaluation',
  data() {
    return {
      radarIndicator: [],
      radarData: [],
      currentTraining: this.$route.query.currentTraining || '',
      currentTitle: this.$route.query.currentTitle || '',
      conversation_id: this.$route.query.conversation_id || '',
      evaluationReasons: ['', '', '', '', ''], // 存储5个维度的评分理由
      isLoading: true, // 添加加载状态
      suggestion: '' // 新增建议字段
    }
  },
  mounted() {
    this.getRadarData();
  },
  methods: {
    async getRadarData() {
      this.isLoading = true; // 开始加载
      try {
        const res = await this.axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'get evaluation',
          inputs: {
              training: this.currentTraining, // 使用动态training
              title: this.currentTitle,
              quit:1
            },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id: this.conversation_id
        }, {
          headers: {
            'Authorization': 'Bearer app-1FfMKOZZ12LF8HXlpXdTopPG',
            'Content-Type': 'application/json'
          }
        });
        console.log(res);
        const data = res.data.answer || '';
        console.log('雷达图数据:', data);
        
        // 解析返回的字符串格式数据
        this.parseEvaluationData(data);
        // 等待DOM更新后再初始化雷达图
        this.$nextTick(() => {
          this.initRadarChart();
        });
      } catch (e) {
        console.error('获取雷达图数据失败:', e);
        // 请求失败时用默认数据
        this.radarIndicator = [
          { name: '冲突转化', max: 5 },
          { name: '策略使用', max: 5 },
          { name: '情绪调节', max: 5 },
          { name: '主动表达', max: 5 },
          { name: '关系维护', max: 5 }
        ];
        this.radarData = [2, 2, 2, 2, 2];
        this.evaluationReasons = [
          '默认评分理由：冲突转化表现良好',
          '默认评分理由：策略使用得当',
          '默认评分理由：情绪调节稳定',
          '默认评分理由：主动表达清晰',
          '默认评分理由：关系维护有效'
        ];
        this.suggestion = '建议：尝试表达自己的感受和理由，并提出具体承诺或改进措施，促进建设性对话。';
        // 等待DOM更新后再初始化雷达图
        this.$nextTick(() => {
          this.initRadarChart();
        });
      } finally {
        this.isLoading = false; // 结束加载
      }
    },
    parseEvaluationData(data) {
      // 设置默认的雷达图维度
      this.radarIndicator = [
        { name: '冲突转化', max: 5 },
        { name: '策略使用', max: 5 },
        { name: '情绪调节', max: 5 },
        { name: '主动表达', max: 5 },
        { name: '关系维护', max: 5 }
      ];
      // 初始化评分数据
      this.radarData = [0, 0, 0, 0, 0];
      this.evaluationReasons = ['', '', '', '', ''];
      // 解析评分部分
      const scorePattern = /(\d+)\.\s*([^:]+):\s*(\d+)/g;
      let match;
      while ((match = scorePattern.exec(data)) !== null) {
        const dimensionName = match[2].trim();
        const score = parseInt(match[3]);
        const dimensionIndex = this.radarIndicator.findIndex(item => item.name === dimensionName);
        if (dimensionIndex !== -1) {
          this.radarData[dimensionIndex] = score;
        }
      }
      // 解析评分理由部分
      // 1. 找到"评分理由："后面的内容
      const reasonStart = data.indexOf('评分理由：');
      if (reasonStart !== -1) {
        // 找到"建议："或"改进建议："的位置，用于分割理由和建议
        const suggestionStart = data.indexOf('建议：');
        const improvementStart = data.indexOf('改进建议：');
        let reasonEnd = data.length;
        
        if (suggestionStart !== -1 && suggestionStart > reasonStart) {
          reasonEnd = suggestionStart;
        } else if (improvementStart !== -1 && improvementStart > reasonStart) {
          reasonEnd = improvementStart;
        }
        
        let reasonText = data.slice(reasonStart + 5, reasonEnd).trim();
        // 2. 用正则分割出每一条理由（支持多行）
        // 匹配"1. xxx\n2. xxx\n..."
        const reasonArr = reasonText.split(/(?=\d+\. )/).filter(Boolean);
        reasonArr.forEach((item, idx) => {
          // 只取前5条
          if (idx < 5) {
            // 去掉前缀"1. "、"2. "等
            const content = item.replace(/^\d+\.\s*/, '').trim();
            // 进一步去掉维度名"冲突转化："等
            const colonIdx = content.indexOf('：');
            let reason = content;
            if (colonIdx !== -1) {
              reason = content.slice(colonIdx + 1).trim();
            }
            this.evaluationReasons[idx] = reason;
          }
        });
      }
      // 解析建议部分
      const suggestionStart = data.indexOf('改进建议：');
      if (suggestionStart !== -1) {
        this.suggestion = data.slice(suggestionStart + 5).trim();
      } else {
        // 尝试其他格式的建议提取
        const suggestionPatterns = [
          /建议：\s*(.+?)(?=\n\d+\.|$)/s,
          /改进建议：\s*(.+?)(?=\n\d+\.|$)/s,
          /建议\s*(.+?)(?=\n\d+\.|$)/s
        ];
        
        for (const pattern of suggestionPatterns) {
          const match = data.match(pattern);
          if (match) {
            this.suggestion = match[1].trim();
            break;
          }
        }
        
        // 如果还是没找到，尝试提取最后一个段落作为建议
        if (!this.suggestion) {
          const lines = data.split('\n').filter(line => line.trim());
          const lastLine = lines[lines.length - 1];
          if (lastLine && !lastLine.match(/^\d+\./)) {
            this.suggestion = lastLine.trim();
          }
        }
      }
      // 兜底：如果有空理由，填充默认
      this.evaluationReasons = this.evaluationReasons.map((r, i) => r || `暂无评分理由`);
      // 控制台输出调试
      console.log('解析后的雷达图数据:', {
        indicator: this.radarIndicator,
        data: this.radarData,
        reasons: this.evaluationReasons
      });
    },
    initRadarChart() {
      const chartDom = document.getElementById('radarChart');
      if (!chartDom) {
        console.warn('雷达图DOM元素不存在，跳过初始化');
        return;
      }
      
      const myChart = echarts.init(chartDom);
      const option = {
        tooltip: {},
        radar: {
          indicator: this.radarIndicator,
          splitLine: {
            lineStyle: {
              color: '#e0d7f7',
            }
          },
          splitArea: {
            areaStyle: {
              color: ['#f6f0ff', '#f3eaff'],
            }
          },
          axisLine: {
            lineStyle: {
              color: '#a084e8',
            }
          },
          name: {
            color: '#a084e8',
            fontSize: 16
          }
        },
        series: [{
          name: '得分',
          type: 'radar',
          areaStyle: {
            color: 'rgba(160, 132, 232, 0.3)'
          },
          lineStyle: {
            color: '#a084e8',
            width: 3
          },
          itemStyle: {
            color: '#a084e8',
            borderColor: '#fff',
            borderWidth: 2
          },
          data: [
            {
              value: this.radarData,
              name: '样例',
              areaStyle: {
                color: 'rgba(160, 132, 232, 0.3)'
              },
              lineStyle: {
                color: '#a084e8',
                width: 3
              },
              itemStyle: {
                color: '#a084e8',
                borderColor: '#fff',
                borderWidth: 2
              }
            }
          ]
        }]
      };
      myChart.setOption(option);
    }
  }
}
</script>